import React, { Component } from 'react';
import TestText from './TestText';
import TestimonialButtons from './TestimonialButtons';

const quotes = [
	{
		author: "Learned Hand",
		content: "No doubt one may quote history to support any cause, as the devil quotes scripture."
	},
	{
		author: "Albert Eienstein",
		content: "It has become appallingly obvious that our technology has exceeded our humanity."
	},
	{
		author: "Steve Jobs",
		content: "A lot of companies have chosen to downsize, and maybe that was the right thing for them. We chose a different path. Our belief was that if we kept putting great products in front of customers, they would continue to open their wallets."
	},
	{
		author: "Issac Asimov",
		content: "I do not fear computers. I fear lack of them."
	},
	{
		author: "Albert Einstein",
		content: "Imagination is more important than knowledge. For knowledge is limited, whereas imagination embraces the entire world, stimulating progress, giving birth to evolution."
	},
	{
		author: "Jeff Atwood",
		content: "We have to stop optimizing for programmers and start optimizing for users."
	},
];

class Testimonials extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			content: "No doubt one may quote history to support any cause, as the devil quotes scripture.",
			author: "Learned Hand",
			number: 0
		}
		this.newQuote = this.newQuote.bind(this);
		this.prevQuote = this.prevQuote.bind(this);
		this.launchTestimony = this.launchTestimony.bind(this);

		let timer = setInterval(() => {
			this.newQuote();
			}, 5000)
		}


	newQuote = () => {
		
		if (this.state.number < quotes.length -1 ) {
			this.setState( currentState => ({number: this.state.number + 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
			clearInterval()
			console.log("boop");
		}	
		else {
			this.setState( currentState => ({number: 0}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}
	};

	prevQuote() {
		
		if (this.state.number > 0 ) {
			
			this.setState( currentState => ({number: this.state.number - 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}

		else {
			this.setState( currentState => ({number: quotes.length - 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}
	};

	launchTestimony = () => {
		setInterval(() => {
			this.newQuote();
		}, 5000)
	}

	componentDidMount(){
		this.launchTestimony();
	}

	render() {
		return (
			<div id = "testimonial-box" className = "w-70 h-auto center pa5 flex flex-column justify-center items-center ba bw1 fruition-blue">
				<TestText quote = {this.state.content} author = {this.state.author} />
				<TestimonialButtons newQuote={this.newQuote} prevQuote={this.prevQuote}/>
			</div>
			)
	}
}

export default Testimonials