import React from 'react';

class LeftArrow extends React.Component {
	constructor(props) {
		super(props)
	}
	render(){
		return (
		<div className='backArrow arrow'>
			<i className="fa fa-arrow-left fa-2x" aria-hidden="true" onClick={this.props.prevQuote}></i>
		</div>
		);
	}
	
}

export default LeftArrow;