import React, { Component } from 'react';

class TestText extends React.Component {
	constructor(props) {
		super(props)
	}

	render(){
		return(
			<div className = "white">
				<h1 id = "text"><i>"{this.props.quote}"</i></h1>
				<h2 id = "author">- {this.props.author}</h2>
			</div>
			)
	}
}

export default TestText