import React from 'react';


const Cog = ({ cogToggleUnlock, cogToggleExplore, cogTogglePlan, cogToggleBuild, cogToggleLaunch, cogState, cogState2, cogState3, cogState4, cogState5 }) => {
	return (
		<div className=''>
			<div className = 'flex flex-row justify-center coglist pa5'>
				{(cogState === false) 
				?	
				<div onClick={cogToggleUnlock} id="unlock" className="cog pointer grow flex justify-center items-center">
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div id ="example" className="step">	
					<p className='mb2 white small-font'>Unlock</p>
					</div>
				</div>
				:
				<div onClick={cogToggleUnlock} id="unlock" className="cog pointer grow flex justify-center items-center">
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div id ="example" className="step">
					<p className='mb2 white cog-font'><u className="f5">Unlock</u> <br />Unlocking the most effective Value Proposition 
					that will create your products powerful,  unique and competitive  fingerprint.</p>
					</div>
				</div>
				}	
				<i id="x1" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleExplore} id="explore" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step">
					{(cogState2 === false)
					?<p className='mb2 white small-font'>Explore</p>
					:<p className='mb2 white cog-font'><u className="f5">Explore</u> <br />Explore the market - that your <br/> product must compete in. 
					Work with Fruition’s team to prepare yourself <br /> and your product for success</p>	
					}
					</div>
				</div>
				<i id="x2" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div>
					<div onClick={cogTogglePlan} id="plan" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(cogState3 === false)
							?<p className='mb2 white small-font'>Plan</p>
							:<p className='mb2 white plan-font'><u className="f5">Plan</u> <br />Plan out –With a diverse <br /> core team, plan how your product <br /> 
							will carefully address <br /> the pain that your customers may 
							currently be experiencing and at the same evaluate
							 how to incorporate disruptive technologies</p>
							}
						</div>
					</div>
					<div id="plan1" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white'>Perfect Team</p></div>
					</div>
					<div id="plan2" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white f6'>Extended Reality</p></div>
					</div>
					<div id="plan3" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white'>Big Data</p></div>
					</div>
					<div id="plan4" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white'>Blockchain</p></div>
					</div>
					<div id="plan5" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white'>IOT</p></div>
					</div>
					<div id="plan6" className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step"><p className='mb2 white'>Artificial Intelligence</p></div>
					</div>
				</div>
				<i id="x3" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleBuild} id="build" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step">
					{(cogState4 === false)
					?<p className='mb2 white small-font'>Build</p>
					:<p className='mb2 white cog-font'><u className="f5">Build</u> <br />Your team of creative <br /> and technological collaborators
					help you actually design and create your MVP <br /> ( minimum viable product ).</p>
					}
					</div>
				</div>
				<i id="x4" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleLaunch} id="launch" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step"><p className='mb2 white small-font'>Launch</p></div>
				</div>
				<i id="x5" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>	
			</div>
		</div>
	);
}

export default Cog
		