import React from 'react';

const Header = () => {
	return (
		<div className = "pt5 w-100 prezi-wrapper relative">
			<div className = "w-100 h-100">
				<iframe className="prezi h-100" width="2000" src="https://prezi.com/view/4A1oergXEZKwefJ3Rc40/embed" 
			webkitallowfullscreen="1" mozallowfullscreen="1" allowfullscreen="1" autoPlay></iframe>
			</div>
		</div>	
	);
}

export default Header;