import React from 'react';

class RightArrow extends React.Component {
	constructor(props) {
		super(props);
	}
	render(){
	 return (
		<div className='nextArrow arrow'>
			<i className="fa fa-arrow-right fa-2x" aria-hidden="true" onClick={this.props.newQuote}></i>
		</div>
		);
	}
	
}

export default RightArrow;