import React, { Component } from 'react';
import LeftArrow from './LeftArrow';
import RightArrow from './RightArrow';

class TestimonialButtons extends React.Component {
	constructor(props) {
		super(props)
	};
	render(){
		return(
			<div className = " mt3 flex flex-row">
				<LeftArrow prevQuote={this.props.prevQuote} />
				<RightArrow newQuote={this.props.newQuote}/>	
			</div>
			
			)
	}
}

export default TestimonialButtons;